<template>
    <div>
<!-- 顶部导航栏 -->
        <div class="cart_menu">
            <div class="cart_menu_content">
                <ul class="cart_menu_ul">
                     <li style="margin-right:10px">
                        欢迎来到购车网：
                    </li>
                    <li>
                        <a href="javascript:;" style="color:red">{{showUname}}</a>
                    </li>
                    <li> 
                        <a href="javascript:;" @click="toIndex">首页</a> 
                    </li>
                    <li>                     
                        <a href="javascript:;" @click="toregister">免费注册</a> 
                    </li>
                    <li class="out">                    
                        <a href="javascript:;" @click="clear_session">退出</a> 
                    </li>
                </ul>
            </div>    
        </div>

        <!-- 搜索栏 -->
        <div class="cart_title">
            <h2>购物车</h2>
            <div class="Search">
                <van-search v-model="search_value" @keydown.13="search_1"  shape="round" background="#3c322d"  show-action placeholder="请输入搜索关键词" >
                    <template #action>
                        <div class="Search_font" @click="search_1" >搜索</div>
                    </template>
                </van-search>   
            </div>        
        </div>  
        <!-- 标签栏 -->
        <div class="th_list">
            <ul>
                <li>
                    <input type="checkbox" v-model="Agreen_all" @change="change_all"/><span> 全选</span>
                </li>
                <li>商品信息</li>
                <li class="th_dj">单价</li>
                <li>数量</li>
                <li>金额</li>
                <li>操作</li>
            </ul>
        </div>

        <!-- 购物车身体部分 -->
        <div class="cart_gwc">
        <!-- 购物车列表 -->    
        <div class="cart_list" :class="{cart_list_1:item.cb}" v-for="(item,i) of list" :key="i">
               <img class="fd_img" :class="{fd_img_hover:i==cent}" :src="baseURL+item.pic" alt=""> 
            <ul>
                <li class="li_ipt" >
                    <input type="checkbox" v-model="item.cb"  @change="change_Single">
                </li>
                <li class="li_img" @mouseenter="img_hover1(i)" @mouseleave="img_hover2()">
                        <img  :src="baseURL+item.pic" alt="">
                </li>
                <li class="li_spxx">
                        <span>{{item.title}}</span>
                </li>
                <li class="li_dj">
                    <span>￥{{(item.price).toFixed(2)}}</span>
                </li>
                <li class="li_sl">
                    <van-stepper v-model="item.count" theme="round" button-size="22"   @plus="add_count(item.id)" @minus="cut_count(item.id)" @change="change_stepper"/>
                </li>
                <li class="li_je">
                    <span>￥{{(item.count)*(item.price).toFixed(2)}}</span>
                </li>
                <li class="li_del">
                    <a href="javascript:;" @click="del(item.id)">删除</a>
                </li>
            </ul>
        </div>
            <div class="cart_foot1">
                <ul>
                    <li class="cart_foot1_del"><a href="javascript:;" @click="select_del">删除选中商品</a></li>
                    <li class="cart_foot1_li">已选商品<span>{{count_quantity}}</span>件</li>
                    <li class="cart_foot1_li">合计：<span>￥{{count_price.toFixed(2)}}</span></li>
                    <li class="cart_foot1_bottom">
                        <van-button   type="danger"  @click="pay">结 算</van-button>  
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import '../css/cart.css';
export default {
    data(){
        return{
            showUname:'',  //顶部用户名
            list:[],      //列表数据
            Agreen_all:false,   //全选状态
            count_price:0,   //合计
            count_quantity:0,  //总件数
            cent:-1,        //判断img是否显示中间量
            search_value:'',  //输入搜索栏内容
            baseURL:this.global.baseURL,//引入路由
        }
    },
    created(){
        this.showuanme();
        this.loadCartList();
    },
    methods:{
        pay(){
            this.$alert("该功能尚未开通！","支付信息");
        },
        search_1(){
            if(this.search_value==''){
                this.loadCartList();
                return;
            }
            this.loadCartList(); //为了解决出现金额NAN问题，先加载一边列表在查找内容
            var obj={key:this.search_value}
            this.$axios.get('/api/cart_Search_value',{params:obj}).then(res=>{
                if(res.data.code==1){
                    this.list=res.data.rows;
                }else{
                    this.list='';
                }
            }).catch(err=>{
                console.log(err);
            })
        },

        //img的显示与隐藏
        img_hover1(i){//显示
            this.cent=i;
        },
        img_hover2(){//隐藏
            this.cent=-1;
        },

        //删除选中的商品
        select_del(){
            //创建变量 str
            var str="";
            //创建循环遍历商品数组，如果cb状态为true拼接id 
            //判断用户是否选中商品
            for(var item of this.list){
                if(item.cb){
                    str+=item.id+",";
                }
            }
            //如果用户没选商品，提示:请选择您需要删除商品
            if(str.length==0){
                this.$alert("请选择商品!","提示信息")
                return;
            }
            //删除提示
            this.$confirm("确定要删除这些商品吗？","删除商品").then(()=>{
                //substring 截取字符串
                //0 起始下标
                //保存几个字符
                str = str.substring(0,str.length-1);
                var obj={ids:str};
                //发送ajax
                this.$axios.get('/api/select_del',{params:obj}).then(res=>{
                    if(res.data.code==1){
                        //为了解决加载删除后没有重新加载页面和 （合计，商品件数）没有改变问题
                        this.change_all();   //点击删除选中商品时，重新加载全选按钮改变事件
                        this.change_Single();  //点击删除选中商品时，重新加载单个复选按钮改变事件
                        this.loadCartList();   //点击删除选中商品时，重新加载购物车列表改变事件
                    }
                }).catch(err=>{
                    console.log(err);
                })
            })
        },
        //单个列表删除按钮
        del(id){
            this.$confirm("确定要删除该商品吗？","删除商品").then(()=>{//提示框
               var obj={id:id};
               //发送ajax请求
                this.$axios.get('/api/del',{params:obj}).then(res=>{
                    if(res.data.code==1){
                        this.loadCartList();
                    }
            }).catch(err=>{
                console.log(err);
            }) 
            })    
        },

        //计数器改变事件（如果选择商品，价格跟着改变）
        change_stepper(){
            var sum2=0;  //选中总价格
            //创建遍历数组中所有元素
            for( var item of this.list){
                if(item.cb){sum2+=(item.count*item.price)}
            }
            this.count_price=sum2;
        },


        //单个商品的change事件，一个商品未选中，全选按钮失效 
        change_Single(){
            var sum=0;   //选择总数量
            var sum2=0;  //选中总价格
            //创建遍历数组中所有元素
            for( var item of this.list){
                if(item.cb){sum++,sum2+=(item.count*item.price)}
            }
            this.count_quantity=sum;
            this.count_price=sum2;
            //如果全部商品的复选框为true  全选按钮为true
            if(sum==this.list.length){
                this.Agreen_all=true;
            }else{
                this.Agreen_all=false;
            }
        },
        //全选按钮的change事件
        change_all(){
            var sum=0;   //选择总数量
            var sum2=0;  //选中总价格
            //创建循环遍历每个元素
            for(var item of this.list){
                item.cb=this.Agreen_all;
                sum++,sum2+=(item.count*item.price)
            }
            if(this.Agreen_all){
            this.count_quantity=sum;
            this.count_price=sum2;   
            }else{
                this.count_quantity=0;
                this.count_price=0;
            }
           
        },
        //数量改变数据库count改变
        //加按钮
        add_count(id){
            var obj={id:id};
            this.$axios.get('/api/add_count',{params:obj}).then(res=>{
                if(res.data.code==1){
                    console.log("加成功！")
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        //减按钮
        cut_count(id){
            var obj={id:id};
            this.$axios.get('/api/cut_count',{params:obj}).then(res=>{
                if(res.data.code==1){
                    console.log("减成功！")
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        //加载购物车列表
        loadCartList(){
            this.$axios.get('/api/Search_cart').then(res=>{
                if(res.data.code==1){
                   this.list=res.data.rows; 
                   for(var item of this.list){
                       item.cb=false;
                       item.xs=false;
                   }
                }else if(res.data.code==-2){
                    this.count_price=0; //全部清除 合计为零
                    this.count_quantity=0;//全部清除 已选商品为零
                    this.list='';
                    this.$alert("购物车空空如也，可以去添加商品哦！")
                    
                }else{
                    this.$router.push('/gwc/product');
                }
            }).catch(err=>{
                console.log(err)
            })
        },
        //免费注册按钮
        toregister(){
            this.$router.push('/gwc/Register')
        },
        //首页按钮
        toIndex(){
                this.$router.push('/gwc/product')
        },
        //退出按钮（清除用户记录 清除session记录）
        clear_session(){
            this.$confirm("退出登录，是否继续？","提示",{
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(()=>{
                this.$axios.get('/api/out').then(res=>{
                    if(res.data.code==1){
                        this.$router.push('/gwc/login');
                    }
                }).catch(err=>{
                    console.log(err);
                })
            })
        }, 
        //顶部用户名显示
        showuanme(){
            this.$axios.get('/api/showUname').then(res=>{
                if(res.data.code==1){
                    this.showUname=res.data.uname;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
    }
}
</script>